<template>
  <div>
    <div class="search-box mt20">
      <div class="field">
        <label>用户ID</label>
        <el-input v-model.trim="searchObj.uid" placeholder="请输入用户ID" class="width200"></el-input>
      </div>
      <div class="field">
        <label>锁定人用户ID</label>
        <el-input v-model.trim="searchObj.lock_uid" placeholder="锁定人用户ID" class="width200"></el-input>
      </div>
      <div class="field">
        <label>邀请人用户ID</label>
        <el-input v-model.trim="searchObj.parent_uid" placeholder="请输入邀请人用户ID" class="width200"></el-input>
      </div>
      <div class="field">
        <label>身份</label>
        <el-select v-model="searchObj.type" placeholder="请选择">
          <el-option
            v-for="item in identifyType"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
      </div>
      <div class="field">
        <el-button type="primary" @click="search">查询</el-button>
        <el-button @click="reset">重置</el-button>
      </div>
      
    </div>
    <div class="align-right">
      <el-button type="primary" :disabled="!exportIds">
        <a :href="downloadUrl" style="color: #fff" v-if="!!exportIds">批量导出信息</a>
        <span v-else>批量导出信息</span>
      </el-button>
    </div>
    <el-table
      class="mt10"
      border
      :data="listData"
      v-loading="loading"
      @selection-change="handleSelectionChange">
      <!-- <el-table-column type="selection" width="55"></el-table-column> -->
      <el-table-column prop="uid" label="用户ID"></el-table-column>
      <el-table-column prop="type" label="身份">
        <template slot-scope="scope">
          <span>{{scope.row.type | filterType}}</span>
        </template>
      </el-table-column>
      <el-table-column prop="lock_uid" label="锁定人ID"></el-table-column>
      <el-table-column prop="lock_type" label="锁定人身份">
        <template slot-scope="scope">
          <span>{{scope.row.lock_type | filterType}}</span>
        </template>
      </el-table-column>
      <el-table-column prop="par_uid" label="邀请人ID"></el-table-column>
      <el-table-column prop="par_type" label="邀请人身份">
        <template slot-scope="scope">
          <span>{{scope.row.par_type | filterType}}</span>
        </template>
      </el-table-column>
      <!-- <el-table-column label="操作">
        <template slot-scope="scope">
          <el-button type="text" size="small" @click="addInviter(scope.row)" v-if="!scope.row.par_uid">添加邀请人</el-button>
        </template>
      </el-table-column> -->
    </el-table>
    <div class="pag-block" v-if="listData.length> 0">
      <div class="fl">每页{{pageSize}}条，共 {{total}} 条记录 第 {{page}}/{{total}} 页</div>
      <el-pagination
        class="fr"
				@current-change="getListbyPage"
			  :current-page="page"
			  background
			  layout="prev, pager, next, jumper"
			  :page-size="pageSize"
			  :total="total">
			</el-pagination>
    </div>
    <!-- 邀请人dialog -->
    <el-dialog
      title="添加邀请人"
      :visible.sync="dialogVisible"
      width="30%">
      <el-form :model="addInviterObj" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
        <el-form-item label="邀请人ID" prop="parent_uid">
          <el-input type="text" v-model="addInviterObj.parent_uid" autocomplete="off" placeholder="请输入邀请人ID"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="submitInviter">提 交</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import api from '@/api'
import { mapGetters } from 'vuex';
import validate from '@/assets/js/validate'
import typesOfRepresent from '@/assets/js/typesOfRepresent'

export default {
  components: {

  },
  watch: {

  },
  data() {
    return {
      exportIds: '',
      searchObj: {
        uid: '',        // 用户ID
        lock_uid: '',   // 锁定人ID
        parent_uid: '', // 邀请人ID
        type: null      // 身份
      },
      identifyType: typesOfRepresent.identifyType,
      listData: [],
      loading: false,
      multipleSelection: [],
      page: 1,
      pageSize: 10,
      total: 0,
      dialogVisible: false,
      addInviterObj: {
        uid: null,
        parent_uid: null
      },
      rules: {
        parent_uid: [
          { validator: validate.checkInviterId, trigger: 'blur' }
        ],
      }
    };
  },
  computed: {
    // 导出
    downloadUrl() {
      return api.relationExportApi + '?ids=' + this.exportIds
    }
  }, 
  created() {
    this.getList();
  },
  mounted() {

  },
  methods: {
    // 是否可以导出
    isExport() {
      if (this.searchObj.uid || this.searchObj.lock_uid || this.searchObj.parent_uid ||  [1, 2, 3].indexOf(Number(this.searchObj.type)) >= 0) {
        return true
      } else {
        return false
      }
    },
    getList() {
			this.loading = true;
			this.$axios.post(api.userRelationQueryApi,  {
        page: this.page -1,
        page_size: this.pageSize,
        ...this.searchObj,
        is_export: this.isExport()
      }).then(res => {
        this.loading = false;
        this.listData = ((res.data.result || {}).list) || []
        this.total = Number((res.data.result || {}).total || 0)
        this.exportIds = (res.data.result || {}).ids || ''
			});
    },
    search() {
       if (this.searchObj.uid.trim() != '' && (isNaN(this.searchObj.uid) || Number(this.searchObj.uid) <= 0)) {
        this.$message.warning('用户ID请输入大于0的数字')
        return;
      }
       if (this.searchObj.lock_uid.trim() != '' && (isNaN(this.searchObj.lock_uid) || Number(this.searchObj.lock_uid) <= 0)) {
        this.$message.warning('锁定人ID请输入大于0的数字')
        return;
      }
       if (this.searchObj.parent_uid.trim() != '' && (isNaN(this.searchObj.parent_uid) || Number(this.searchObj.parent_uid) <= 0)) {
        this.$message.warning('邀请人请输入大于0的数字')
        return;
      }
      this.page = 1;
      this.getList();
    },
    reset() {
      this.searchObj = {
        uid: '',        // 用户ID
        lock_uid: '',   // 锁定人ID
        parent_uid: '', // 邀请人ID
        type: null      // 身份
      };
    },
    // 添加邀请人dialog
    addInviter(row) {
      this.dialogVisible = true;
      this.addInviterObj.uid = row.uid;
      this.$refs['ruleForm'] && this.$refs['ruleForm'].resetFields();
    },
    // 添加邀请人
    submitInviter() {
      let inviteAjax = params => {
        this.$axios.post(api.relationAddApi, this.addInviterObj).then(res => {
          if (res.data && res.data.result === 1) {
            this.$message.success('添加邀请人成功！');
            this.getList();
          } else {
            this.$message.success(res.msg || '操作失败');
          }
        });
      }
      this.$refs['ruleForm'].validate((valid) => {
        if (valid) {
          inviteAjax();
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
    //监听多选变更
		handleSelectionChange(val) {
			this.multipleSelection = [];
			for (var i = 0; i < val.length; i++) {
				this.multipleSelection.push(val[i].uid);
			}
    },
    getListbyPage(page) {
			this.page = page;
			this.getList();
		},
  },
  filters: {
    filterType(value) {
      return typesOfRepresent.getLabelByValue(typesOfRepresent.identifyType, value);
    }
  }
};
</script>

<style lang="scss" scoped>

</style>



